html * {
  outline: 0px solid red;
}
html, body {
  margin: 0px;
  padding: 0px;
  }

header{
  height: 70px;
  background: linear-gradient(to bottom, #0000ff 0%, #000f00 100%);
  text-align: center;
  color: white;
 }

.content {
  position: absolute;
  min-width: 100%;
  min-height: 100%;
  background-image: url('/img/orchestra.jpg');
 }

footer{
  position: absolute;
  top: 100%;
  width: 100%;
  height: 70px;
  background: linear-gradient(to bottom, #001100 0%, #0000ff 100%);
  text-align: center;
  color: white;
}

.header_div{
  max-width: 1200px;
  margin: auto;
}
.menu_logo{
margin: auto;
display: flex;
justify-content: space-between;
}
.logo{
  height: 70px;
  }
.menu {
  display: flex;
  justify-content: space-between;
  margin: auto 0 auto 0;
  width: 450px;  
}
.menu_button {
  height: 30px;
  width: 100px;
  border: 3px solid white;
  border-radius: 0 10px;
  margin: 5px;
  }
.menu_button > p {
   margin: 5px;
}